<script>
	import moment from "moment";
	import WeekDay from "./WeekDay.svelte";
	
	let config = {
		previousWeeks: 2,
		followingWeeks: 3,
		weekDays: ["Mo", "Di", "Mi", "Do", "Fr", "Sa", "So"]
	};
	let weekArray = [];
	(function() {
		for (let i=0; i<config.previousWeeks; i++) {
			weekArray.push(i-config.previousWeeks);
		}
		weekArray.push(0);
		for (let i=0; i<config.followingWeeks; i++) {
			weekArray.push(config.previousWeeks-i+1);
		}
		weekArray.sort((a, b) => {
			return a - b;
		});
		console.log(weekArray);
	})();
</script>

<div class="container">
	<div class="calendar">
		<div class="header">
				<div></div>
				{#each config.weekDays as weekDay}
					<div>{weekDay}</div>
				{/each}
		</div>
		{#each weekArray as weekDiff}
			<div class="week">
				<div>KW {moment().week()+weekDiff}</div>
				{#each config.weekDays as weekDay, weekDayIndex}
					<div>
						<WeekDay {weekDiff} {weekDayIndex}/>
					</div>
				{/each}
			</div>
		{/each}
	</div>
</div>



