<script>
	import moment from "moment";
	export let weekDiff;
	export let weekDayIndex; // 0-6 Mo-So
	
	let weekDayNow = moment().isoWeekday()-1; // 0-6 Mo-So
	let weekDayDiff = weekDayIndex - weekDayNow;

	let thisDate = moment().add(weekDiff, 'weeks').add(weekDayDiff, 'days');
	let isToday = moment().isSame(thisDate, "day");
	let isFirstOfMonth = thisDate.date() == 1;
</script>

<span class="outerFrame" class:isToday="{isToday}" class:isFirstOfMonth="{isFirstOfMonth}">
	<span class="day">{thisDate.format("DD.")}</span>
	<span class="month">{thisDate.format("MM.")}</span>
</span>

<style>
	.outerFrame {
		padding: 6px;
	}
	.month {
		font-size: 11px;
		font-weight: 600;
		margin-left: -4px;
		opacity: 0.3;
	}
	.isToday {
		background: rgba(10,200,60,0.3);
	}
</style>
